'use strict';

define('actions/directives/ActionEditor',['require','angular','text!./actionEditor.tpl.html'],function(require) {
	var angular = require('angular');
	var template = require('text!./actionEditor.tpl.html');

	var ActionEditor = function($compile, language) {
		return {
			template: template,
			restrict : 'E',
			scope : {
				action : "=",
				actions : "=",
			},
			controller : function($scope) {
				$scope.language = language.getText();
			}
		};
	};

	return [ '$compile', 'language', ActionEditor ];
});

