'use strict';

define('interactions/models/Interaction',['require'],function(require) {
	var Interaction = function(modelBuilder) {
		var _service = modelBuilder.getModel('Interaction', {}, {
			get : {
				method : 'GET',
				cache : false
			}
		});

		return _service;
	};

	return [ 'modelBuilder', Interaction ];
});

